import { Injectable } from '@angular/core';
import { BaseService } from '../../services/base.service';
import { HttpClient } from '@angular/common/http';
import { CadastraServicoViewModel } from './cad-servicosVM/cadastra-servico-view-model';
import { Observable } from 'rxjs';
import { FormGroup } from '@angular/forms';


@Injectable({
  providedIn: 'root'
})
export class CadServicosService extends BaseService {

  constructor(private http: HttpClient) {
    super();
  }

  postServicos(servico: CadastraServicoViewModel, imagem: File): Observable<CadastraServicoViewModel> {
    let options = {
      headers: this.getHeader()
    };

    const formData: FormData = new FormData();
    if (imagem !== null || imagem !== undefined) {
      formData.append('files', imagem, imagem.name);
    }
    formData.append('servico', JSON.stringify(servico));
        
    return this.http.post<CadastraServicoViewModel>(`${this.UrlV1}servico/v1/cadastrar`, formData, options);

  }

  getServicos(): Observable<CadastraServicoViewModel[]> {
    let options = {
      headers: this.getAuthHeader()
    };

    return this.http.get<CadastraServicoViewModel[]>(`${this.UrlV1}servico/v1/listaservicos`, options);

  }


}
