import { VantagemViewModel } from './vantagem-view-model';

export class CadastraServicoViewModel {
    Id: number;
    Titulo: string;
    ImagemPath: string;
    ImagemData: string;
    DescricaoCurta: string;
    DescricaoLonga: string;
    ListaVantagens: VantagemViewModel[] = new Array<VantagemViewModel>();
    Ativo: boolean;

    constructor(_titulo?: string, _imagempath?: string, _descricaocurta?: string,
        _descricaolonga?: string, _listavantagens?: VantagemViewModel[], _ativo?: true) {
        this.Titulo = _titulo;
        this.ImagemPath = _imagempath;
        this.DescricaoCurta = _descricaocurta;
        this.DescricaoLonga = _descricaolonga;
        this.ListaVantagens = _listavantagens;
        this.Ativo = _ativo;
    }
}
